/*--- Error ---*/
.errContainer {
   color: #ff6060;
   display: flex;
   flex-direction: column;
   align-items: center;
   text-align: center;
   margin: 50px 0 20px 0;
   height: 100%;
   flex-grow: 1;
}

.big404 {
   font-family: 'Montserrat';
   font-style: normal;
   font-weight: 700;
   font-size: 100px;
   left: 0%;
   right: 0%;
   top: 0%;
   bottom: 0%;
   display: flex;
   align-items: center;
   text-align: center;
   color: #ff6060;
	@include media-query(small){
		font-size: 150px;
	}
	@include media-query(medium){
		font-size: 200px;
	}
	@include media-query(large){
		font-size: 288px;
	}
}
h1 {
   font-weight: 500;
}

.return {
   padding-top: 12em;
}
a {
   color: #ff6060;
}

/*--- Fin Error ---*/
